import React, {useState} from 'react';
import Table from './Table';
import Formulario from './Formulario';

const Section = () => {
  // valor inicial, creamos funcion que se va a setear la funcion handleclick funcionUpdate nombre de funcion
    const[count, setCount] = useState(0);
    // darle el valor incial:
    // en los componentes era this.state = {count:0}
const [datos, setDatos] = useState(''); //  el valor incial es null(vacio)

function handleChange(e){
setDatos(e.target.value);
}

function handleSubmit(e){
    e.preventDefault()
    alert(`El nombre ingresado es ${datos}`);
}


    function handleClick(){
        // cada vez que hagan click
        setCount(count+1);
    }

    return (<section>
<form onSubmit={handleSubmit}>
<label>Nombre</label>
<input type="text" value={datos} onChange={handleChange}/>
<input type="submit"/>

</form>
<p>Haz hecho click {count} veces</p>
  <button onClick={handleClick}>Click </button>
 <Table data={datos}/> 
      {/* pasar el props */}
<Formulario/>


    </section>
    );
}

export default Section;