import React from 'react';
import Imagen from './Imagen';
import NombreUsuario from './NombreUsuario'

const Header = (props) => {
    const user= {name:"Jacqueline", apellido:"Paredes"}
    return ( <header className="encabezado">
        <div><h1>ComIT</h1></div>
            <div><Imagen/></div>
            <div className="NOMBRES">
         <NombreUsuario user={user}/></div>
         
        
       
    </header>
    );
}

// JSX -AZUCAR SINTACTICO- javascript con html/xml

export default Header;